
.article-content {
  max-width: 1100px;
  margin: 0 auto;
  overflow: hidden;
  padding: 0 20px;
  ul {
    width: 100%;

  }
  .article-date {
    width: 11.18%;
    height: 0.086%;
    background: #eeeeee;
    display: inline-block;
    text-align: center;
    padding: 23px 0;
    margin: 31px 86px 31px 0;
    strong {
      color: #4c4d4d;
    }
    p {
      color: #5a5b5b;
      margin: 0;
      padding: 0;
    }
  }
  .article-info {
    width: 73.27%;
    display: inline-block;
    p {
      font-size: 1.4rem;
      color: #878888;
      margin: -12px 0 0 0;
      padding: 0;

    }

  }
}

.article-content {
  ul {
    margin: 15px 0 100px 0;
    padding: 0;
    li {
      display: flex;
      align-items: center;
      justify-content: space-between;
      list-style: none;
      border-bottom: 1px dashed #b0b0b0;
      h3 {
        color: #000000;
        display:inline-block;
      }
    }
  }
}

.article-protect-tab {
  text-align: center;
  padding: 4rem 0;
  white-space: nowrap;
  span {
    border: 1px solid #aaaaaa;
    width: 13.5rem;
    display: inline-block;
    text-align: center;
    height: 40px;
    line-height: 38px;
    border-radius: 22px;
    font-size: 1.8rem;
    font-weight: bold;
    margin: 0 1rem;
    cursor: pointer;
  }
  span:hover {
    color: #ffffff;
    background: #5cc18c;
    border-color: #5cc18c;
  }
}

.article-protect-body {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 5rem;
  img {
    max-width: 40%;
  }
  ul {
    margin: 0;
    width: 53%;
    display: inline-block;
    li {
      display: block;
      a:hover {
        cursor:pointer;
        h3 {
          color: #359461;
        }
        span {
          color: #359461;
        }
      }
      h3 {
        margin: 0;
        display: inline-block;
      }
      span {
        float: right;
        color: #000000;
        font-weight: bold;
      }
      p {
        margin: 0.5rem 0 1rem;
        font-size:1.4rem;
      }
    }
  }
}

.article-info{
  a:hover{
    h3{
      color:#28905a;
    }
  }
  span{
    color:#c3c3c3;
    display:none;
  }
  i{
    font-style:normal;
    color:#000000;
    float:right;
    display:none;
  }
}
@media only screen and (max-width:@default-max-md){
  .article-content{
    ul{
      margin: 15px 0 30px!important;
    }
  }
  .article-date{
    display:none !important;
  }
  .article-info{
    width:100%!important;
    h3{
      margin:13px 0 0 0;
    }
   i{
     display:block!important;
     margin:17px 0 0 0 ;
   }
    span{
      display:block!important;
      margin-bottom:5px;
    }
    p {
      display: none !important;
    }
  }
    .article-protect-body{
    display:none;
  }
  .article-protect-tab{
    padding: 3rem 0 0 0!important;
    span{
      width:10rem !important;
      border-radius:1rem!important;
    }
  }
}
@media only screen and (min-width:@default-max-sm) and(max-width:@default-max-mds){
  .article-protect-tab{

    span{
      width:8rem !important;
      margin: 0 auto !important;
    }
  }
  .article-content{
    ul{
      margin: 15px 0 0px!important;
    }
  }
}
@media only screen and(max-width:@default-max-sm){
  .article-protect-tab{
    span{
      width:6rem!important;
      font-size:1.4rem;
      margin:0 auto!important;
    }
  }
  .article-info{
    h3{
      font-size:1.4rem!important;
    }
    span{
      font-size:1.4rem!important;
    }
  }
  .article-content{
    ul{
      margin:15px 0 0!important;
    }
  }
}