.index-main-md{
 max-width:1100px;
  margin:0 auto;
  overflow: hidden;
  padding:0 20px;
  .left-area{
    display:inline-block;
    width:48%;
    height:367px;
    float:left;
    background:url("../images/banner3.jpg")no-repeat;
    background-size:contain;
  }
  .right-area{
    display:inline-block;
    width:50%;
    float:right;
    p{
      font-size:2rem;
      margin:0;
    }
    span{
      color:#a3a3a3;
    }
    div{
      width:75%;
      margin:0 auto;
      img{
        width:100%;
      }
    }
  }
}
@media only screen and (min-width: @default-max-md) and (max-width:@default-max-bg){
  .index-main-md-body{
    div{
      height:210px !important;
    }
  }
}
@media only screen and (min-width: @default-max-sm) and (max-width:@default-max-md){
  .index-main-md-body{
  ul{
    > li:nth-child(2){
      margin: 0 5px !important;
    }
    > li:nth-child(1) div:nth-child(1){
      height:170px !important;
      background: url("../images/team1s.jpg")no-repeat center center / contain !important;
    }
    > li:nth-child(2) div:nth-child(1){
      height:170px !important;
      background: url("../images/team2s.jpg")no-repeat center center / contain !important;
    }
    > li:nth-child(3) div:nth-child(1){
      height:170px !important;
      background: url("../images/team3s.jpg")no-repeat center center / contain !important;
    }
    span{
      display:none !important;
    }
  }
    > span{
      display:block !important;
    }
  }
}
@media (min-width:@default-max-md){
  .prevbtn{
    display:none !important;
  }
}
@media (max-width: @default-max-md) {
  .left-area{
    width: 100%!important;
    background: url("../images/banner3s.jpg") no-repeat!important;
    -webkit-background-size: 100% 100%!important;
    height: 180px !important;
    margin-bottom:20px;
  }
  .right-area{
    width:100% !important;

    p{
      display: none;
    }
    span{
      text-indent: 2rem;
      padding:20px;
    }
    div{
      display:none;
    }
  }
}
.index-main-md-body{
  ul{
    margin:0;
    padding:0;
    text-align: center;
    white-space: nowrap;
    li{
      width:31.333%;
      list-style: none;
      display:inline-block;
      div{
        width:100%;
      }
      p{
        margin:0;
        padding:0;
        font-size: 1.6rem;
        font-weight: bold;
      }
      span{
        display:block;
        color:#ababab;
        font-size:1.4rem;
        margin-top:1.5rem;
        white-space: normal;
      }
    }
    > li:nth-child(2){
      margin: 0 25px;
    }
    > li:nth-child(1) div:nth-child(1) {
      height: @about-team-height;
      background: url("../images/team1.jpg") no-repeat center center / contain;

    }
    > li:nth-child(2) div:nth-child(1) {
      height: @about-team-height;
      background: url("../images/team2.jpg") no-repeat center center / contain;
    }
    > li:nth-child(3) div:nth-child(1) {
      height: @about-team-height;
      background: url("../images/team3.jpg") no-repeat center center / contain;
    }
  }
  > span{
    display: none;
    color: #ababab;
    font-size: 1.4rem;
    white-space: normal;
    width: 70%;
    margin: 1.5rem auto 0;
    text-align:center;
  }
}
.about-main-footer{
  max-width:1100px;
  margin-bottom:5rem;
  span{
    display:block;
    width:100%;
    height:272px;
    background:url("../images/smfeature1.png") no-repeat center center / contain;
  }
}
@media only screen and(max-width:@default-max-bg){
.about-main-footer{
  margin-top:-6rem ;
  margin-bottom:0!important;
}
}
@media only screen and(min-width:@default-max-mds) and(max-width:@default-max-md){
  .about-main-footer{
    margin:-7rem 0 0 0 !important;

  }
}
@media only screen and(min-width:@default-max-sm) and(max-width:@default-max-mds){
  .about-main-footer{
    height:22rem;
  }
}
@media only screen and (max-width:@default-max-sm){
  .about-main-footer{
    margin-top:-95px !important;
    height: 18rem;
  }

  .index-main-md-body ul>li:nth-child(2){
    margin:0;
  }
  .index-main-md-body ul li div{
    height:100px !important;
  }
  .prevbtn{
    width:30px !important;
    height:30px !important;
    margin-top:1.5rem !important;
    margin-left:1.5rem !important;
  }
}
.prevbtn{
  background: url("../images/prevbtn.png") center center/contain no-repeat;
  width: 50px;
  height: 50px;
  display: block;
  position: absolute;
  z-index: 2;
  margin-left: 2rem;
  margin-top: 2rem;
}
