.article_list_more_header{
  width:1100px;
  margin:0 auto;
  padding: 60px 0 10px;
  border-bottom: 2px solid #eeeeee;
  div{
    padding-left: 13px;
  }
  a{
    color:#7c7c7c;
  }
}
.article_list_more_pages{
  max-width:1100px;
  margin:0 auto;
  text-align: center;
  padding:20px 0;
  ul{
    margin:0;
    padding:0;
    display:block;
  }
  li{
    list-style: none;
    display:inline-block;
    padding:0 0.5rem;
    a{
      color:#878787;
    }
  }
}