.project-case{
  padding: 0 20px;
  width: 100%;
  > div {
    overflow:hidden;
    padding-bottom:1rem;
  }
  > div:nth-child(2){
    img{
      float:right;
    }
  }
  img{
    display: inline-block;
    width:40%;
    float:left;
  }

  .projec-case-right{
    display: inline-block;
    width: 55%;
    margin-left:15px;
    h6{
      margin:0;
      padding:0;
      font-size:1.6rem;
    color:#737373;
    }
    p{
      margin:0;
      padding:0;
      font-size:1.4rem;
      color:#a9a9a9;
    }
  }
}
