.news-info{
  max-width:1100px;
  text-align: center;
  overflow: hidden;
  margin-bottom: 10rem;
  h2{
    display: block;
    width: 84px;
    margin: 40px auto;
    padding: 3px 0;
    border-bottom: 2px solid #000;
  }
  > div{
    ul{
      float:right;
      margin:0;
      padding:0;
      width:60%;
      li{
        list-style: none;
        float: left;
        margin-bottom: 23px;
        > div{
          overflow: hidden;
          max-width: 78%;
        }
        img{
          width:165px;
          float:left;
        }
        p{
          float: left;
          margin: 5px 0 0 10px;
          font-weight: bold;
        }
        span{
          float: left;
          width: 71%;
          text-align: left;
          color: #ababab;
          margin: 5px 0 0 10px;
        }
        a{
          border: 1px solid #999;
          color: #000;
          display: block;
          width: 90px;
          text-align: center;
          height: 28px;
         /* margin: 63px auto 0;*/
          float: right;
        }
      }

    }
  }
}
