.contact-right {
  position: absolute;
  right: 0;
  margin-top: -26px;
  margin-right: 10px;
}

.contact-left {
  color: #f2545d;
}

.contact {
  max-width: 1100px;
  margin: 0 auto;
  padding: 50px 0;
  text-align: center;

  img {
    width: 60px;
    height: 60px;
  }
  ul {
    width: 38%;
    float: right;
  }
  li {
    list-style: none;
    position: relative;
    overflow: hidden;
    border-top: 1px dashed #cccccc;
    padding: 30px 0;
  }
  span {
    float: left;
  }
  h2 {
    display: inline-block;
    margin: 0 0 0 10px;
    padding: 0;
    float: left;
  }
  p {
    max-width: 300px;
    overflow: hidden;
    display: inline-block;
    float: left;
    padding: 0;
    margin: 33px 0 0 -79px;
  }
}
.contact-header{
  max-width:1100px;
  text-align:center;
  margin: 100px auto 60px;
  h1{
    margin:0;
  }
  span{
    color:#b4b4b4;
    display:block;
  }
}
.contact-icon{
  max-width:1100px;
  margin:0 auto;
  padding:0;
  text-align:center;
  white-space: nowrap;
  li{
    list-style:none;
    display:inline-block;
    width:30%;
    text-align:center;
    vertical-align: top;
    span{
      display:block;
      margin:0 auto;
      white-space: normal;
    }
  }
  > li:nth-child(1) > span:nth-child(1){
    width:70px;
    height:70px;
    background:url("../images/contact1.png") no-repeat center center /contain;
  }
  > li:nth-child(2) > span:nth-child(1){
    width:70px;
    height:70px;
    background:url("../images/contact2.png") no-repeat center center /contain;
  }
  > li:nth-child(3) > span:nth-child(1){
    width:70px;
    height:70px;
    background:url("../images/contact3.png") no-repeat center center /contain;
  }
}
@media only screen and(min-width:@default-max-md) and (max-width:@default-max-bg){
  .contact-header{
    margin:20px auto 20px!important;
  }
  .contact-icon li span{
    font-size:1rem;
  }
  .contact-icon{
    > li:nth-child(1) > span:nth-child(1){
      width:50px!important;
      height:50px!important;
    }
    > li:nth-child(2) > span:nth-child(1){
      width:50px!important;
      height:50px!important;
    }
    > li:nth-child(3) > span:nth-child(1){
