
@font-face {font-family: "iconfont";
  src: url('../fonts/iconfont.eot?t=1495180682438'); /* IE9*/
  src: url('../fonts/iconfont.eot?t=1495180682438#iefix') format('embedded-opentype'), /* IE6-IE8 */
  url('../fonts/iconfont.woff?t=1495180682438') format('woff'), /* chrome, firefox */
  url('../fonts/iconfont.ttf?t=1495180682438') format('truetype'), /* chrome, firefox, opera, Safari, Android, iOS 4.2+*/
  url('../fonts/iconfont.svg?t=1495180682438#iconfont') format('svg'); /* iOS 4.1- */
}


.font{
  font-family:"iconfont" !important;
}
.iconfont {
  font-family:"iconfont" !important;
  font-size:16px;
  font-style:normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
@media (max-width: @default-max-md){
  header{
    display: none;
  }
}
.header-top{
  height: 100%;
  vertical-align: middle;
  margin-bottom: 16px;
  margin-top: 16px;
  overflow: hidden;
  .width-center{
    padding-left:20px;
  }
}
.width-center{
  max-width: 1100px;
  margin: 0 auto;
  overflow:hidden;
}

.header-title{
  margin-left: 12px;
  margin-top: -6px;
  float: left;
}
.div-inline{
  display: inline-block;
  height: 100%;
  vertical-align: middle;
  strong{
    font-size:3rem;
    color:#28905a;
  }
  span{
    display:block;
    margin:-6px 0 0 2px;
  }
  img{
    margin-left: 15px;
  }
}
.header-logo{
display:inline-block;
  float:left;
}
.header-right{
  float:right;
  span{
    display:block;

  }
  span:nth-child(2){
    font-size:1.8rem;
  }
}


//导航栏样式
.header-nav {
  background: #28905a;
 /* overflow: hidden;*/
  nav{
    max-width: 1100px;
    margin: auto;
  /*  overflow:hidden;*/
    position:relative;
   > ul{
      list-style: none;
      margin:0;
      padding:0;
      overflow: hidden;
     >   li {
       float:left;
       padding:0;
       text-align:center;
       min-width:120px;
       > a{
         font-size: 1.8rem;
         color: #fff;
         width: 100%;
         /* min-width: 120px; */
         /* height: 100%; */
         display: inline-block;
         padding: 1.6rem 0;
       }
     }
     li:hover{
       background: #5cc18c;
     }
    }
  }
  .secondary-menu{
    img{
      width:118px;
      height:40px;
    }
      ul{
        margin:0;
        padding:0;
        li{
          list-style: none;
        }
      }
  }
  /*ul{
    list-style: none;
    margin:0;
    padding:0;
    overflow: hidden;
  }*/
/*  li {
    float:left;
    padding:1.6rem 14px;
    text-align:center;
    min-width:120px;
  }*/

  /*li:hover{
    background: #f1404b;
  }*/
  a{
    font-size:1.8rem;
    color: #ffffff;
  }
}
@media only screen and (min-width:641px) {
  .header-nav {
    & ul {
      display: block;

      & > li {
        float: left;

      }
    }
  }
}
//轮播样式
.am-slider-default .am-control-nav li a{
  background-color: rgba(255,255,255,1);
  box-shadow: none;

}
.am-slider-default .am-control-nav li a:hover{
  background-color: rgba(255,255,255,1);
}
.am-slider-default .am-direction-nav a{
  top:40%;
  height:58px;
}
.am-slider-default .am-control-nav{
  bottom:23px;
}
.am-slider-default .am-direction-nav a.am-next:before{
  content:"";
}
.am-slider-default .am-direction-nav a:before{
  content:"";
}
.am-slider-default:hover .am-next{
  opacity: 1;
}
.am-slider-default .am-control-nav li a.am-active{
  box-shadow:0 0 2px 2px #ffffff;
}
.am-prev{
  background: url('../images/prev.png');
}
.am-next{
  background: url("../images/next.png");
}
.am-slider-default{
  margin:0;
}
//底部样式
.footer{
  background:#29905b ;
  color:#ffffff;
  .am-btn-success{
    background-color: #29905b;
    border: none;
    outline:none;
  }
  .am-btn-success:active,.am-btn-success:visited{
    outline:none;
    border:none;
  }
  i{
    font-style: normal;
  }
  .footer-pc{
    max-width:1100px;
    margin:0 auto;
    padding:40px 20px;
    img{
      float:right;
    }
  }
  .footer-phone{
    text-align: right;
    max-width:1100px;
    margin:0 auto;
    padding:0 20px;
  }
  ul{
    margin:0;
    padding:0;
    display:inline-block;
    height:150px;
    li{
      list-style: none;
      p{
        margin:0;
      }
    }
  }

}
@media only screen and(max-width:@default-max-md){
.footer-pc{
  display:none;
}
}
@media only screen and(min-width:@default-max-md){
  .footer-phone{
    display:none;
  }
}
