.article-on {
  a {
    color: #f2545d;
  }
}

.article-contents {
  h4 {
    text-align: center;
    padding: 65px 0;
  }
  main {
    padding: 0 35px 100px;
    display: block;
    text-indent: 2rem;
  }
}

.header-article-list {
  max-width:1100px;
  margin:0 auto;
  padding:20px;
  .next-btn{
    float:right;
  }
  a {
    color: #000000;
  }
}

@media only screen and (min-width:@default-max-mds) and (max-width:@default-max-bg){
  .article-contents{
    padding:0;
    h4{
      padding:20px 0;
      margin:0;
    }
    main{
      padding: 0 35px!important;
    }
  }
}
@media only screen and (min-width:@default-max-sm) and (max-width:@default-max-mds){
  .article-contents{
    padding:0;
    h4{
      font-size:1.4rem!important;
      padding: 10px 0;
      margin:0;
    }
    main{
      padding: 0 10px!important;
      font-size:1.2rem!important;
      p{
        margin:0!important;
      }
    }
  }
  .header-article-list{
    padding:0 10px 20px;
    span{font-size:1.2rem;}
  }
}
@media only screen and(max-width:@default-max-sm){
  .article-contents{
    padding:0;
    h4{
      font-size:1.2rem!important;
      padding: 10px 0 ;
      margin:0;
    }
    main{
      padding: 0 10px!important;
      font-size:1rem!important;
      p{
        margin:0!important;
      }
    }
  }
  .header-article-list{
    padding:0;
    span{
      font-size:1rem;
    }
  }
}
